/* Variables */
$vin-blue: #5bb7db;
$vin-green: #60b044;
$vin-red: #ff0000;

/* Styles */
body {
  font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  background-image: url("https://subtlepatterns.com/patterns/concrete_seamless.png");
  line-height: 1.4em;
  color: #4d4d4d;
  min-width: 230px;
  max-width: 900px;
  margin: 20px auto 50px;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  font-smoothing: antialiased;
  font-weight: 400;
}

td {
	padding: 12px;
}

h2 {
	color: $vin-blue;
}

.technology {
  padding: 2px 5px;

  .icon {
    width: 75px;
    height: auto;
  }

  .title {
    text-align: center;
    font-weight: bolder;
  }
}

/* Related to Elemental UI */
.ui.inverted.segment a {
  color: white;
}



